import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Route, RouterModule } from '@angular/router';
import { ClienteListComponent } from './containers/cliente-list/cliente-list.component';
import { ClienteFormComponent } from './containers/cliente-form/cliente-form.component';
import { SharedModule } from 'app/shared/shared.module';
import { FuseAlertModule } from '@fuse/components/alert';
import { FormEnderecoComponent } from './components/form-endereco/form-endereco.component';
import { DetalhesClienteComponent } from './components/detalhes-cliente/detalhes-cliente.component';
import { NgxMaskModule } from 'ngx-mask';

const routes: Route[] = [
  { path: '', component: ClienteListComponent },
  { path: 'novo', component: ClienteFormComponent },
  { path: ':id/editar', component: ClienteFormComponent },
];

@NgModule({
  declarations: [ClienteListComponent, ClienteFormComponent, FormEnderecoComponent, DetalhesClienteComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    SharedModule,
    FuseAlertModule,
    NgxMaskModule.forRoot(),
  ],
})
export class ClienteModule {}
