import { BaseResourceModel } from 'app/shared/models/base-resource.model';

export class Cliente extends BaseResourceModel {
  constructor(
    public id?: number,
    public codigoSupergas?: string,
    public nome?: string,
    public enderecos?: Endereco[],
    public cnpj?: string,
    public email?: string,
    public telefone?: string,
    public observacoes?: string,

    ) {
    super();
  }
}

export class ClienteResponse {
  constructor(
    public content?: Cliente[],
    public totalElements?: number,
    public size?: number,
    public numberOfElements?: number
  ) {}
}

export class Endereco {
  constructor(
    public id?: number,
    public logradouro?:  string,
    public numero?: number,
    public complemento?:  string,
    public bairro?:  string,
    public cep?:  string,
    public cidade?: Cidade,
    public cidadeId?: number,
    public clienteId?: number
  ) {}
}

export class EnderecoResponse {
  constructor(
    public id?: number,
  ) {}
}

export class Cidade {
  constructor(
      public id?: number,
      public nome?: string,
      public estado?: Estado,
  ) {}
}

export class Estado {
  constructor(
    public id?: number,
    public nome?: string,
    public codigoUf?: number,
    public uf?: string,
    public regiao?: number,
  ) {}
}
