import { Component, Injector, OnInit } from '@angular/core';
import { Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { BaseResourceFormComponent } from 'app/shared/containers/base-resource-form';
import { ClienteService } from '../../api/cliente.service';
import { FormEnderecoComponent } from '../../components/form-endereco/form-endereco.component';
import { Cliente, ClienteResponse, Endereco } from '../../models/cliente.model';
import toastr from 'toastr';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { EnderecoService } from '../../api/endereco.service';

@Component({
  selector: 'app-cliente-form',
  templateUrl: './cliente-form.component.html',
  styleUrls: ['./cliente-form.component.scss'],
})
export class ClienteFormComponent extends BaseResourceFormComponent<
  Cliente,
  ClienteResponse
> {
  enderecos: Endereco[] = [];

  constructor(
    protected clienteService: ClienteService,
    protected injector: Injector,
    protected _fuseConfirmationService: FuseConfirmationService,
    protected enderecoService: EnderecoService,
    private _matDialog: MatDialog
  ) {
    super(injector, new Cliente(), clienteService);
  }

  ngOnInit(): void {
    this.enderecos = [];
    super.ngOnInit();
    if (this.currentAction === 'editar') {
      setTimeout(() => {
        this.carregarEnderecos();
      }, 500);
    }
  }

  protected buildResourceForm() {
    this.resourceForm = this.formBuilder.group({
      codigoSupergas: [null, [Validators.required, Validators.minLength(2)]],
      nome: [null, [Validators.required, Validators.minLength(2)]],
      cnpj: [null, [Validators.required, Validators.minLength(12)]],
      email: [null, [Validators.required, Validators.email]],
      telefone: [null, [Validators.required]],
      observacoes: [null],
      enderecos: [this.enderecos],
    });
  }

  protected carregarEnderecos(): void {
    this.enderecos = this.resource.enderecos;
  }

  adicionarEndereco(): void {
    const enderecoNovo: Endereco = {
      id: this.enderecos.length + 1,
    };

    const dialogRef = this._matDialog.open(FormEnderecoComponent, {
      data: {
        idCliente: this.resource.id,
        endereco: enderecoNovo,
        adicionar: true,
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      if (!this.resource.id && result) {
        if (!this.enderecos) this.enderecos = [];
        this.enderecos.push(result);
        return;
      }

      if (result) {
        toastr.success('Endereço cadastrado com sucesso!', '', {
          timeOut: 10000,
          progressBar: true,
          closeButton: true,
        });

        this.ngOnInit();
      }
    });
  }

  editarEndereco(endereco: Endereco): void {
    const dialogRef = this._matDialog.open(FormEnderecoComponent, {
      data: {
        idCliente: this.resource.id,
        endereco: endereco,
        adicionar: false,
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      if (result) {
        toastr.success('Endereço atualizado com sucesso!', '', {
          timeOut: 10000,
          progressBar: true,
          closeButton: true,
        });

        this.ngOnInit();
      }
    });
  }

  excluirEndereco(endereco: Endereco): void {
    const dialogRef = this._fuseConfirmationService.open({
      title: `Deseja realmente excluir o endereco selecionado?`,
      message: '',
      icon: {
        show: true,
        name: 'heroicons_outline:exclamation',
        color: 'warn',
      },
      actions: {
        confirm: {
          show: true,
          label: 'Confirmar',
          color: 'warn',
        },
        cancel: {
          show: true,
          label: 'Cancelar',
        },
      },
      dismissible: false,
    });

    dialogRef.afterClosed().subscribe((result) => {
      if (result === 'confirmed') {
        if (!this.resource.id) {
          this.enderecos = this.enderecos.filter((e) => e.id !== endereco.id);
          return;
        }
        this.enderecoService.delete(endereco.id).subscribe(
          (res) => {
            toastr.success(`Endereço excluído com sucesso!`, '', {
              timeOut: 10000,
              progressBar: true,
              closeButton: true,
            });
            this.enderecos = this.enderecos.filter((e) => e.id !== endereco.id);
          },
          (error) => {
            toastr.error(
              'Não foi possível excluir o endereço selecionado',
              error.error.error,
              {
                timeOut: 10000,
                progressBar: true,
                closeButton: true,
              }
            );
          }
        );
      }
    });
  }

  submitForm() {
    if (this.currentAction === 'novo') {
      this.enderecos = this.enderecos.map((e) => {
        e.id = null
        return e;
      });
      this.resourceForm.get('enderecos').setValue(this.enderecos);
    }

    super.submitForm();
  }

  protected createPageTitle(): string {
    return 'Cadastro de Novo Cliente';
  }

  protected editionPageTitle(): string {
    const clienteNome = this.resource.nome || '';
    return 'Editando Cliente: ' + clienteNome;
  }
}
