import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Estado } from '../models/estados.interface';
import { environment as env } from 'environments/environment';

@Injectable({
  providedIn: 'root'
})
export class EstadoService {

  readonly apiPath = '/estados';

  constructor(private http: HttpClient) { }

  getAllEstados(): Observable<Estado[]> {
    return this.http.get<Estado[]>(`${env.api}${this.apiPath}`);
  }

  pegarTodasCidadeDoEstado(idEstado: number): Observable<Estado[]> {
    return this.http.get<Estado[]>(`${env.api}${this.apiPath}/${idEstado}/cidades`);
  }

}
