import { Component, OnInit, Inject, Injector } from '@angular/core';
import { Validators } from '@angular/forms';
import {
  MatDialog,
  MAT_DIALOG_DATA,
  MatDialogRef,
} from '@angular/material/dialog';
import { BaseResourceFormComponent } from 'app/shared/containers/base-resource-form';
import { EnderecoService } from '../../api/endereco.service';
import { EstadoService } from '../../api/estado.service';
import { Endereco, EnderecoResponse } from '../../models/cliente.model';
import { Cidade, Estado } from '../../models/estados.interface';
import toastr from 'toastr';
import * as uuid from 'uuid';

@Component({
  selector: 'app-form-endereco',
  templateUrl: './form-endereco.component.html',
  styleUrls: ['./form-endereco.component.scss'],
})
export class FormEnderecoComponent extends BaseResourceFormComponent<
  Endereco,
  EnderecoResponse
> {
  endereco: Endereco;
  cidades: Cidade[] = [];
  estados: Estado[] = [];

  constructor(
    public matDialogRef: MatDialogRef<FormEnderecoComponent>,
    @Inject(MAT_DIALOG_DATA)
    public data: { idCliente: number; endereco: Endereco; adicionar: boolean },
    protected enderecoService: EnderecoService,
    protected injector: Injector,
    protected estadosService: EstadoService
  ) {
    super(injector, new Endereco(), enderecoService);
  }

  ngOnInit(): void {
    this.endereco = new Endereco();
    super.ngOnInit();
    setTimeout(() => {
      this.estadosService
        .getAllEstados()
        .subscribe((estados) => (this.estados = estados));
    }, 0);
  }

  estadoSelecionado(): void {
    const idEstado = this.resourceForm.get('estado').value;
    this.estadosService
      .pegarTodasCidadeDoEstado(idEstado)
      .subscribe((cidades) => (this.cidades = cidades));
  }

  fecharJanela(): void {
    this.matDialogRef.close();
  }

  salvar(): void {
    this.endereco = this.resourceForm.value;
    if (!this.data.idCliente) {
      this.endereco.id = this.data.endereco.id;
      this.endereco.cidade = this.cidades.find(
        (c) => c.id === this.endereco.cidadeId
      );
      this.endereco.cidade.estado = this.estados.find(
        (e) => e.id === this.endereco['estado']
      );
      this.matDialogRef.close(this.endereco);
      return;
    }

    this.endereco.clienteId = this.data.idCliente;

    if (this.data.adicionar) {
      this.enderecoService.insert(this.endereco).subscribe(
        () => {
          this.matDialogRef.close(true);
        },
        (error) => {
          toastr.error(
            'Não foi possível adicionar o endereço!',
            error.error.error,
            {
              timeOut: 10000,
              progressBar: true,
              closeButton: true,
            }
          );
        }
      );
    }

    if (!this.data.adicionar) {
      this.endereco.id = this.data.endereco.id;
      this.enderecoService.update(this.endereco).subscribe(
        () => {
          this.matDialogRef.close(true);
        },
        (error) => {
          toastr.error(
            'Não foi possível atualizar o endereço!',
            error.error.error,
            {
              timeOut: 10000,
              progressBar: true,
              closeButton: true,
            }
          );
        }
      );
    }
  }

  loadResource(): void {
    if (this.data.endereco.cep) {
      setTimeout(() => {
        this.estadosService
          .pegarTodasCidadeDoEstado(this.data.endereco.cidade.estado.id)
          .subscribe((cidades) => (this.cidades = cidades));
        this.resourceForm.patchValue(this.data.endereco);
        this.resourceForm.patchValue({
          estado: this.data.endereco.cidade.estado.id,
          cidadeId: this.data.endereco.cidade.id,
        });
      }, 0);
    }
  }

  protected buildResourceForm() {
    this.resourceForm = this.formBuilder.group({
      estado: [null, [Validators.required]],
      cidadeId: [null, [Validators.required]],
      logradouro: [null, [Validators.required]],
      numero: [null, [Validators.required]],
      bairro: [null, [Validators.required, Validators.minLength(3)]],
      cep: [null, [Validators.required, Validators.minLength(8)]],
      complemento: [null],
    });
  }

  protected createPageTitle(): string {
    return 'Cadastro de Novo Endereco';
  }

  protected editionPageTitle(): string {
    return 'Editando endereco';
  }
}
